.header{
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
}
.logo{
    width: 100px;
    padding: 1rem;
}
.nav-items > ul{
    display: flex;
    list-style-type: none;
    font-size: 15px;
}
.nav-items > ul > li{
    padding: 10px;
    margin: 10px;
}
.res-container{
    display: flex;
    flex-wrap: wrap;
}
.res-card{
    margin: 5px;
    padding: 5px ;
    width: 250px;
    height: 500px;
}
.res-card:hover{
    cursor: pointer;
    border: 1px solid black;
}
.search{
    padding: 10px;
}
.res-logo{
    width: 100%;
    height: 150px;
}